<template>
  <v-card flat>
    <v-card-text>
      <v-container
        fluid
        grid-list-md>
        <v-layout
          row
          wrap>
          <v-flex xs12>
            <v-img :src="detail.url"/>
          </v-flex>
          <v-flex xs12>
            <p class="headline">{{ detail.title }}</p>
            <v-text-field
              v-if="isOwner"
              v-model="newTitle"
              class="headline"
              label=""
              @keyup.enter="changeTitle"
            />
          </v-flex>
          <v-flex md8>
            <material-card
              color="commentinfo"
              title="">
              <v-list three-line>
                <template v-for="(item, index) in comments">
                  <v-subheader
                    v-if="item.username"
                    :key="index">{{ item.username }}:</v-subheader>
                  <v-list-tile
                    :key="index"
                    avatar>
                    <v-list-tile-content>
                      <v-list-group v-text="item.content"/>
                      <v-list-tile-sub-title
                        class="text-xs-right"
                        v-text="item.cdate"/>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-btn
                        icon
                        ripple
                        color="tertiary"
                        flat
                        @click="report(item.cid)">
                        <v-icon>mdi-alert-circle</v-icon>
                      </v-btn>
                    </v-list-tile-action>
                  </v-list-tile>
                  <v-divider
                    :key="index"
                    :inset="true"/>
                </template>
              </v-list>
              <v-textarea
                v-model="input"
                :rules="rules"
                label=""
                @keyup.enter="submit"/>
              <v-btn
                color="cButton"
                @click="submit">
                <v-icon>edit</v-icon>
              </v-btn>
            </material-card>
          </v-flex>
          <v-flex md4>
            <v-layout
              row
              wrap
              class="text-xs-left">
              <v-flex md6>
                <v-btn
                  v-if="isStar"
                  flat
                  icon
                  color="pink"
                  @click="onChange(0)">
                  <v-icon>star</v-icon>
                  :{{ detail.star }}
                </v-btn>
                <v-btn
                  v-else
                  flat
                  icon
                  color="tertiary"
                  @click="onChange(0)">
                  <v-icon>mdi-star-outline</v-icon>
                  :{{ detail.star }}
                </v-btn>
              </v-flex>
              <v-flex md6>
                <v-btn
                  v-if="isThumb"
                  flat
                  icon
                  color="lightblue"
                  @click="onChange(1)">
                  <v-icon>thumb_up</v-icon>
                  :{{ detail.thumb }}
                </v-btn>
                <v-btn
                  v-else
                  flat
                  icon
                  color="tertiary"
                  @click="onChange(1)">
                  <v-icon>mdi-thumb-up-outline</v-icon>
                  :{{ detail.thumb }}
                </v-btn>
              </v-flex>
              <v-flex md1>
                <v-icon small>loyalty</v-icon>
              </v-flex>
              <v-flex
                v-for="tag in detail.tags"
                :key="tag"
                md4>
                <router-link :to="/search/ + tag">{{ tag }}</router-link>
              </v-flex>
              <v-flex md12>
                <v-divider/>
              </v-flex>
              <v-flex md4></v-flex>
              <v-flex md8>
                <div>
                  <router-link :to="authorPage">{{ detail.author }}</router-link>
                </div>
              </v-flex>
              <v-flex md4></v-flex>
              <v-flex md8>:{{ detail.upload }}</v-flex>
              <v-flex md4></v-flex>
              <v-flex md8>:{{ detail.count }}</v-flex>
              <v-flex md12>
                <template v-if="isOwner">
                  <v-btn
                    block
                    color="pDelete"
                    dark
                    @click="delPic"></v-btn>
                </template>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card-text>
  </v-card>
</template>

<script>
//  id
export default {
  props: {
    pid: Number
  },
  data: () => ({
    //  id  api
    detail: {
      author: '',
      count: 0,
      star: 0,
      thumb: 0,
      title: '',
      upload: '',
      url: '',
      tags: []
    },
    // 
    rules: [v => !!v || ''],
    isThumb: Boolean,
    isStar: Boolean,
    input: '',
    comments: [
      {
        username: '',
        content: '',
        cdate: '',
        cid: ''
      }
    ],
    // 
    isOwner: false,
    // 
    authorPage: '',
    newTitle: ''
  }),

  watch: {
    input: 'validateField'
  },
  created: function () {
    this.getImageDetails()
    this.getComment()
  },

  methods: {
    validateField () {
      this.$refs.form.validate()
    },
    // 
    judge () {
      this.$api.user
        .getUid({
          username: this.detail.author
        })
        .then(res => {
          //  id 
          this.authorPage = '/user-profile/' + res.data.uid + '/type/info'
          if (res.data.uid.toString() === window.localStorage.uid) {
            this.isOwner = true
          }
        })
        .catch(err => {})
      if (
        window.localStorage.authority == 'root' ||
        window.localStorage.authority == 'admin'
      ) {
        this.isOwner = true
      }
    },
    // 
    submit () {
      if (this.input != '') {
        this.$api.image
          .addComment({
            iid: this.pid,
            uid: window.localStorage.uid,
            content: this.input
          })
          .then(res => {
            if (res.data.status === 200) {
              this.$message({
                type: 'success',
                message: ''
              })
              this.getComment()
              this.input = ''
            }
          })
          .catch(err => {})
      }
    },
    // 
    getComment () {
      this.$api.image
        .getCommentsByiid({
          iid: this.pid
        })
        .then(res => {
          this.comments = res.data.comments
        })
        .catch(err => {})
    },
    // 
    report (cid) {
      this.$confirm(' ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$api.image
            .report({
              cid: cid
            })
            .then(res => {
              this.$message({
                type: 'success',
                message: res.data.message
              })
            })
            .catch(err => {})
        })
        .catch(() => {})
    },
    //
    onChange (item) {
      switch (item) {
        // 
        case 0:
          this.isStar = !this.isStar
          this.$api.image
            .setStar({
              uid: window.localStorage.uid,
              iid: this.pid,
              isStar: this.isStar
            })
            .then(res => {
              this.getImageDetails()
            })
            .catch(res => {})
          break
        // 
        case 1:
          this.isThumb = !this.isThumb
          this.$api.image
            .setThumb({
              uid: window.localStorage.uid,
              iid: this.pid,
              isThumb: this.isThumb
            })
            .then(res => {
              this.getImageDetails()
            })
            .catch(err => {})
          break
      }
    },
    // 
    delPic () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$api.user
            .delPic({
              iid: this.pid
            })
            .then(res => {
              console.log(res)
              this.$router.replace('/pixel')
              location.reload()
              this.$message({
                type: 'success',
                message: '!'
              })
            })
            .catch(err => {})
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    //  url
    getImageDetails () {
      this.$api.image
        .getImageDetails({
          iid: this.pid,
          uid: window.localStorage.uid
        })
        .then(res => {
          console.log(res)
          var data = res.data
          data.url = this.$api.root.getUrl(data.url)
          this.detail = data
          this.isStar = data.isStar
          this.isThumb = data.isThumb
          // 
          this.judge()
        })
        .catch(err => {})
    },
    // 
    changeTitle () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$api.image
            .updateTitle({
              iid: this.pid,
              title: this.newTitle
            })
            .then(res => {
              this.$message({
                type: 'success',
                message: res.data.message
              })
              this.newTitle = ''
              this.getImageDetails()
            })
            .catch(err => {})
        })
        .catch(() => {})
    }
  }
}
</script>
